.customizePackage{
  width: 100%;
  background-color: rgb(235, 235, 235);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  flex-direction: row;
  flex-wrap: wrap;
}
.customPackage1{
  width: 45%;
  background-color: white;
  border-radius: 10px;
  margin: 10px;
  border:1px solid #007bff;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 60px 0px;
  transition: 0.2s;
}
.customPackage1:hover{
  box-shadow: 0px 0px 25px gray;
  transition: 0.2s;
}
.customPackage2{
  width: 45%;
  transition: 0.2s;
  background-color: #3695fa;
  border-radius: 10px;
  margin: 10px;
  border:1px solid #007bff;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 60px 0px;
}
.customPackage2:hover{
  box-shadow: 0px 0px 25px gray;
  transition: 0.2s;
}
.Card1Head{
  color: #3695fa;
  font-size: 25px;
  font-weight: bold;
}
.Card2Head{
  color: white;
  font-size: 25px;
  font-weight: bold;
}
.CardDesc{
  color: gray;
  font-size: 20px;
  font-weight: 200;
  margin-top: 10px;
  text-align: center;
}
.CardDesc2{
  color: white;
