  color: white;
  font-weight: 600;
  margin: 5% 1%;
  background-color: #007bff;
  cursor: pointer;
  border-radius: 10px;
}

.Package1Btn{
  padding: 15px 50px;
  border:1px solid #007bff;
  border-radius: 30px;
  background-color: #3695fa;
  color: white;
  font-weight: 600;
  margin-top: 30px;
  transition: 0.3s;
}
.Package1Btn:hover{
  padding: 15px 100px;
  transition: 0.3s;
}
.Package2Btn{
  padding: 15px 50px;
  border:1px solid white;
  border-radius: 30px;
  background-color: #3695fa;
