header .nav ul {
  display: flex;
  margin: 0;
  padding: 0;
  float: left;
  width: 100% !important;
  list-style: none;
}

header .nav ul {
  margin: 0;
  padding: 0;
  float: left;
  width: auto;
  width: initial;
}

header .nav ul li {
  float: left;
  margin-right: 45px;
}

header .nav ul li a {
  text-decoration: none;
  font-size: 14px !important;
  color: #000;
  position: relative;
  padding: 39.5px 10px;
  box-sizing: border-box;
  width: auto;
  width: initial;
  float: left;
  transition: all 0.5s ease;
  font-weight: 500;
}

header .nav ul li a:before {
  content: "";
  position: absolute;
  background: #d20b08;
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  transition: all 0.5s ease;
  transition-delay: 0.4s;
  z-index: -1;
}

header .nav ul li a:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 7px;
  background: #d20b08;
  bottom: 0;
  left: 0;
  transition: all 0.5s ease;
  z-index: -2;
}

header .nav ul li:nth-child(2) a:before,
header .nav ul li:nth-child(2) a:after {
  background: #ff5621;
}

header .nav ul li:nth-child(3) a:before,
header .nav ul li:nth-child(3) a:after {
  background: #ed1e79;
}

header .nav ul li:nth-child(4) a:before,
header .nav ul li:nth-child(4) a:after {
  background: #1782ed;
}

header .nav ul li:nth-child(5) a:before,
header .nav ul li:nth-child(5) a:after {
  background: #2bce61;
}

