.view {
  border: 3px solid #d5d5d5;
  border-radius: 45px;
  display: table;
  margin: 0 auto;
  padding: 31px 41px;
  text-transform: uppercase;
  transition: all 0.5s ease;
  position: relative;
  overflow: hidden;
}
.view:before {
  content: "";
  position: absolute;
  width: 0%;
  height: 50%;
  background: #d5d5d5;
  left: 0;
  top: 0;
  transition: all 0.5s ease;
  z-index: 1;
}
.view:hover:before {
  width: 100%;
}
.view:after {
  content: "";
  position: absolute;
