/*The button*/
.services .view {
  border: 3px solid #d5d5d5;
  border-radius: 45px;
  display: table;
  padding: 14px 41px;
  text-transform: uppercase;
  transition: all 0.5s ease;
  position: relative;
  overflow: hidden;
  margin: 0;
  margin-right: auto;
  margin-bottom: 25px;
}

.services .view:before {
  content: "";
  position: absolute;
  width: 0%;
  height: 50%;
  background: #d5d5d5;
  left: 0;
  top: 0;
  transition: all 0.5s ease;
  z-index: 1;
}

.services .view span {
  font-size: 15px;
  color: #000;
  font-weight: bold;
  z-index: 2;
  position: relative;
}

.services .view:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 50%;
  background: #d5d5d5;
  right: 0;
  bottom: 0;
  transition: all 0.5s ease;
  z-index: 1;
  transition-delay: 0.4s;
}

/*The button*/
.services .view+h4 {
  display: block;
  color: #434da2;
  font-size: 20px;
  margin-bottom: -23px;
